import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Upload from '../../assets/images/upload-1.png';
import ENUMS from '../../assets/js/enum';
import Dropzone from 'react-dropzone';
import OpenModal from '../OpenModal/OpenModal';

const useStyles = makeStyles((theme) => ({
  container: {
    paddingTop: '40px'
  },
  formInput: {
    width: '100%',
    height: '40px',
    fontWeight: 'bold',
    fontSize: '18px',
    textAlign: 'center',
    borderRadius: '5px',
    outline: 'none',
    background: '#707070',
    border: 'none',
    color: '#fff',
    padding: '0',
    '&::placeholder': {
      color: '#fff'
    }
  },
  button: {
    background: '#707070',
    width: '100%',
    marginTop: '20px',
    padding: '0',
    height: '150px',
    '&:hover': {
      background: '#707070',
    }
  },
  dBlock: {
    display: 'block',
    color: '#fff',
    fontSize: '14px',
    textTransform: 'none',
    fontWeight: 'bold'
  },
  buttonText: {
    paddingTop: '60px',
    height: '150px',
    background: '#707070',
    marginTop: '20px'
  },
  extensions: {
    fontWeight: 'normal'
  },
  getStarted: {
    width: '100%',
    background: '#fff',
    color: '#000',
    fontWeight: 'bold',
    marginTop: '20px',
    textTransform: 'none',
    fontSize: '18px',
    '&:hover': {
      background: '#FE784B'
    }
  },
  file: {
    marginTop: '10px',
    display: 'block'
  },
  lowOpacity: {
    opacity: '0.7'
  },
  errors: {
    color: 'red',
    marginTop: '7px',
    display: 'block'
  }
}));



function UploadForm() {
  const [selectedFile, setSelectedFile] = useState('');
  const [validFile, setvalidFile] = useState(true);
  const [fieldsVal, setFieldsVal] = useState({});
  const [errorsVal, setErrorsVal] = useState({});
  const [successVal, setSuccessVal] = useState('');

  const classes = useStyles();
  
  const validationOnUpload = (file) => {
      var _validFileExtensions = [".psd", ".Xd", ".Sketch", ".Fig"];
      setSelectedFile(`${file}`);
        var sFileName = file;
        if (sFileName.length > 0) {
          var blnValid = false;
          for (var j = 0; j < _validFileExtensions.length; j++) {
            var sCurExtension = _validFileExtensions[j];
            if (sFileName.substr(sFileName.length - sCurExtension.length, sCurExtension.length).toLowerCase() === sCurExtension.toLowerCase()) {
              blnValid = true;
              setvalidFile(true);
              break;
            }
          }
          if (!blnValid) {
            setSelectedFile();
            setvalidFile(false);
            return false;
          }
        }
      return true;
  };

  const handleValidation = () => {
    let fields = fieldsVal.fields;
    let errors = {};
    let formIsValid = true;

    if (Object.keys(fieldsVal).length === 0 && fieldsVal.constructor === Object) {
      errors["file"] = "Please select a file.";
      errors["email"] = "Cannot be empty";
      formIsValid = false;
      return;
    };

    if (!selectedFile) {
      errors["file"] = "Please select a file.";
      formIsValid = false;
    }

    //Email
    if(!fields["email"]){
      formIsValid = false;
      errors["email"] = "Cannot be empty";
    }

    if(typeof fields["email"] !== "undefined"){
       let lastAtPos = fields["email"].lastIndexOf('@');
       let lastDotPos = fields["email"].lastIndexOf('.');

       if (!(lastAtPos < lastDotPos && lastAtPos > 0 && fields["email"].indexOf('@@') === -1 && lastDotPos > 2 && (fields["email"].length - lastDotPos) > 2)) {
          formIsValid = false;
          errors["email"] = "Email is not valid";
        }
   }  

   setErrorsVal({errors: errors});
   return formIsValid;
};

const contactSubmit = (e) => {
  e.preventDefault();

  if(handleValidation()){
    setSuccessVal("Form submitted");
  }

};
const handleChange = (field, e) => {
  setErrorsVal({});
  let fields = fieldsVal;
  fields[field] = e.target.value;        
  setFieldsVal({fields});
};

const openModal = () => {
  console.log('Open Modal');
};
  return (
    <div className={classes.container}>
    <Grid container justify="center">
    <Grid item lg={6} md={9} xs={12}>
      <form className={classes.root} onSubmit={(e) => contactSubmit(e)}>
        <input placeholder='Email' type='email' onChange={(e) => handleChange("email", e)} className={classes.formInput} />
        { errorsVal.errors && errorsVal.errors['email'] && <span className={classes.errors}>{errorsVal.errors && errorsVal.errors['email']}</span> }
        <Dropzone onDrop={(acceptedFiles) => validationOnUpload(acceptedFiles[0].name)}>
          {({ getRootProps, getInputProps }) => (
            <section>
              <div {...getRootProps()}>
                <input {...getInputProps()} />
                <div className={classes.buttonText}>
                  <span className={classes.dBlock}><img src={Upload} alt='Upload' /></span>
                  <span className={classes.dBlock}>{ENUMS.DRAG_TEXT}</span>
                  <span className={`${classes.dBlock} ${classes.lowOpacity} ${classes.extensions}`}>{ENUMS.EXTENSION_TEXT}</span>
                </div>
              </div>
            </section>
          )}
        </Dropzone>
        { errorsVal.errors && errorsVal.errors['file'] && <span className={classes.errors}>{errorsVal.errors && errorsVal.errors['file']}</span> }
        {selectedFile &&  <span className={classes.file}>{selectedFile}</span> }
        <Button variant="outlined" className={classes.getStarted} onClick={(e) => contactSubmit(e)}>{ENUMS.BUTTON}</Button>
        { successVal && <span className={classes.success}>{successVal}</span> }
        <p className={classes.lowOpacity}>{ENUMS.TERM_TEXT}</p>
      </form>
      </Grid>
      </Grid>
      { !validFile && <OpenModal parentCallback={openModal} modalName='errorModal' content={ENUMS.ERROR} /> }
    </div>
  );
}

export default UploadForm;